import React from 'react'
import styled from 'styled-components'

import Image from 'gatsby-image'
import { graphql, useStaticQuery } from 'gatsby'

export const TestimonialContainer = styled.section`
    position: relative;
    display: flex;
    height: 80rem;
    overflow: hidden;

`

export const ImageContainer = styled.div`
    position: absolute;
    width: 100%;

    .bg-image {
        top: -25rem
    }

`

export const ContentCol = styled.div`
    position: relative;
    z-index: 99;
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    &:nth-child(even){
        background-color: rgba(255,255,255, .6);
    }
`

export const UserIcon = styled.div`
    width: 8rem;
    height: 8rem;
    background-color: gray;
    border-radius: 50%;
    margin-bottom: 3rem;
`

export const UserName = styled.h3`
    font-weight: bolder;
    text-transform: uppercase;
    margin-bottom: 3rem;
    font-size: 2.5rem;
`

export const Quote = styled.p`
    max-width: 50rem;
    font-weight: 900;
    text-transform: uppercase;
    text-align: center;
    font-size: 2rem;
`

const TestimonialSection = () => {
    const data = useStaticQuery(graphql`
        query {
            desktop: file(relativePath: { eq: "products_overview.jpg" }) {
            childImageSharp {
                fluid {
                ...GatsbyImageSharpFluid_withWebp
                }
            }
            }
        }
    `)

    const imageData = data.desktop.childImageSharp.fluid

    return (
        <TestimonialContainer>
            <ImageContainer>
                <Image className='bg-image' alt='bg image' fluid={imageData} />
            </ImageContainer>
            <ContentCol>
                <UserIcon />
                <UserName>Gréta K.</UserName>
                <Quote>"Po novej kuchyni som snívala už dlho a pán Turanský mi to splnil"</Quote>
            </ContentCol>
            <ContentCol></ContentCol>

        </TestimonialContainer>
    )
}

export default TestimonialSection
