import React from 'react'
import styled from 'styled-components'
import { graphql, StaticQuery } from 'gatsby'
import Image from 'gatsby-image'
import BackgroundImage from 'gatsby-background-image'

export const LogoContainer = styled.div`
    width: 25rem;
    height: 25rem;
    margin-bottom: 3rem;
`

export const HeroContent = styled.div`
    width: 100%;
    height: 100%;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
`

export const SocialLinksContainer = styled.div`
    width: 25rem;
    display: flex;
    justify-content: space-between;
`

export const SocialIcon = styled.div`
    cursor: pointer;
    width: 5rem;
    height: 5rem;
`

const HeroSection = ({ className }) => {
    return (
        <StaticQuery
            query={graphql`
                query {
                    desktop: file(relativePath: { eq: "background-landing.jpg" }) {
                        childImageSharp {
                            fluid {
                            ...GatsbyImageSharpFluid_withWebp
                            }
                        }
                    }
                    brandLogo: file(relativePath: { eq: "logo-landing.png" }) {
                        childImageSharp {
                            fluid {
                            ...GatsbyImageSharpFluid_withWebp
                            }
                        }
                    }
                    igLogo: file(relativePath: { eq: "ig-logo.png" }) {
                        childImageSharp {
                            fluid {
                            ...GatsbyImageSharpFluid_withWebp
                            }
                        }
                    }
                    fbLogo: file(relativePath: { eq: "fb-logo.png" }) {
                        childImageSharp {
                            fluid {
                            ...GatsbyImageSharpFluid_withWebp
                            }
                        }
                    }
                }
                `}
            render={data => {
                // Set ImageData.
                const imageData = data.desktop.childImageSharp.fluid
                const brandLogo = data.brandLogo.childImageSharp.fluid
                const igLogo = data.igLogo.childImageSharp.fluid
                const fbLogo = data.fbLogo.childImageSharp.fluid
                return (
                    <BackgroundImage
                        Tag="section"
                        className={className}
                        fluid={imageData}
                        backgroundColor={`#040e18`}
                    >
                        <HeroContent>
                            <LogoContainer>
                                <Image fluid={brandLogo} />
                            </LogoContainer>
                            <SocialLinksContainer>
                                <SocialIcon>
                                    <Image fluid={fbLogo} />
                                </SocialIcon>
                                <SocialIcon>
                                    <Image fluid={igLogo} />
                                </SocialIcon>
                            </SocialLinksContainer>
                        </HeroContent>
                    </BackgroundImage>
                )
            }}
        />
    )


}
const HeroSectionContainer = styled(HeroSection)`
  width: 100%;
  height: 100vh;
  background-position: bottom center;
  background-repeat: repeat-y;
  background-size: cover;

`

export default HeroSectionContainer
