import React from 'react'
import styled from 'styled-components'
import { graphql, useStaticQuery } from 'gatsby'
import Image from 'gatsby-image'
import { ParallaxProvider, Parallax } from 'react-scroll-parallax';

const ProductsOverviewSection = () => {
    const data = useStaticQuery(graphql`
        query {
            desktop: file(relativePath: { eq: "products_overview.jpg" }) {
            childImageSharp {
                fluid {
                ...GatsbyImageSharpFluid_withWebp
                }
            }
            }
        }
    `)

    const imageData = data.desktop.childImageSharp.fluid

    const products = [
        {
            title: "Matné",
            image: "matne.jpg",
            y: ['-50%', '50%']
            // y: [-100, 100]
        },
        {
            title: "Lesklé",
            image: "leskle.jpg",
            y: ['0%', '0%']
        },
        {
            title: "Drevodekor",
            image: "drevodekor.jpg",
            y: ['-50%', '50%']
            // y: [-100, 100]

        },
        {
            title: "Fólia",
            image: "folia.jpg",
            y: ['0%', '0%']
        },
        {
            title: "Iný nábytok",
            image: "ine.jpg",
            y: ['-50%', '50%']
            // y: [-100, 100]

        },
    ]



    return (
        <ParallaxProvider>
            <ProductsOverviewSectionContainer>
                <SectionTitle>Kuchyne</SectionTitle>
                <ContentContainer>
                    {
                        products.map((product, idx) => {

                            return (
                                <Parallax
                                    key={idx}
                                    y={product.y}
                                    tagOuter="figure"

                                >
                                    <ProductCard >
                                        <ImageContainer>
                                            <Image alt={product.title} fluid={imageData} />
                                        </ImageContainer>
                                        <ProductContent>
                                            <ContentTitle>{product.title}</ContentTitle>
                                        </ProductContent>
                                    </ProductCard>
                                </Parallax>
                            )
                        })
                    }

                </ContentContainer>
            </ProductsOverviewSectionContainer>
        </ParallaxProvider>
    )
}

export default ProductsOverviewSection

export const ProductsOverviewSectionContainer = styled.section`
    /* height: 100vh; */
    padding: 15rem 3rem 25rem;
    position: relative;
`

export const SectionTitle = styled.h2`
    position: absolute;
    text-transform: uppercase;
    font-size: 16rem;
    font-weight: 900;
    opacity: .5;
    top: 5rem;
`


export const ContentContainer = styled.div`
    margin-top: 12rem;
    display: flex;
    justify-content: space-evenly;

`


export const ProductCard = styled.div`
    cursor: pointer;
    width: 25rem;
    height: 35rem;
    position: relative;
    overflow: hidden;
    background-color: gray;
    /* box-shadow: 1rem 2rem 4rem 0 rgba(0,0,0, 0.05); */
    transition: transform .2s ease-out;
    box-shadow: 2rem 2rem 4rem 0 rgba(0,0,0, 0.05);

    &:hover {
        transform: scale(1.05);
    }


`

export const ImageContainer = styled.div`
    position: absolute;
    width: 60rem;
    height: 100%;
    top:0;
`

export const ProductContent = styled.div`
    position: absolute;
    z-index: 99;
    background-color: black;
    color: #fff;
    bottom: 2rem;
    width: 100%;
    text-align: center;

    &::before {
        content: " ";
        position: absolute;
        left: 0;
        right: 0;
        top: -5rem;
        height: 15rem;
        background-color: black;
        transform: skew(0deg, -15deg);
        z-index: -1;
    }
`

export const ContentTitle = styled.h2`
    text-transform: uppercase;
`