import React from 'react'
import styled from 'styled-components'

import { graphql, useStaticQuery } from 'gatsby'
import Image from 'gatsby-image'

export const ProjectOverviewContainer = styled.section`
    position: relative;
    padding: 10rem 3rem;
`

export const Heading = styled.h2`
    /* position: absolute; */
    text-transform: uppercase;
    font-size: 16rem;
    font-weight: 900;
    opacity: .5;
`
export const ContentContainer = styled.div`
    /* margin: 15rem 0; */
`

export const ProjectOverviewCard = styled.div`
    display: flex;

    &:nth-child(even){
        flex-direction: row-reverse
    }
`

export const CardCol = styled.div`
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    p {
        margin-bottom: 3rem;
    }

    &:nth-child(even) {
        padding: 4rem;
    }
`

export const OptionsContainer = styled.div`
    /* display: flex; */
    width: 100%;
`

const ProjectOverview = () => {
    const data = useStaticQuery(graphql`
        query {
            desktop: file(relativePath: { eq: "products_overview.jpg" }) {
                childImageSharp {
                    fluid {
                    ...GatsbyImageSharpFluid_withWebp
                    }
                }
            }
            materialProjects: file(relativePath: { eq: "materials-projects.png" }) {
                childImageSharp {
                    fluid {
                    ...GatsbyImageSharpFluid_withWebp
                    }
                }
            }
        }
    `)

    const imageData = data.desktop.childImageSharp.fluid
    const materialProjects = data.materialProjects.childImageSharp.fluid


    return (
        <ProjectOverviewContainer>
            <Heading>Projekty</Heading>

            <ContentContainer>

                <ProjectOverviewCard>
                    <CardCol>
                        <Image alt='Project image' fluid={imageData} />
                    </CardCol>
                    <CardCol>
                        <p>
                            Lorem ipsum, dolor sit amet consectetur adipisicing elit. Commodi repudiandae sed aperiam ut! Mollitia hic aliquid eaque, consequatur sit illo?
                            Gumbo beet greens corn soko endive gumbo gourd. Parsley shallot courgette tatsoi pea sprouts fava bean collard greens dandelion okra wakame tomato. Dandelion cucumber earthnut pea peanut soko zucchini.
                        </p>
                        <OptionsContainer>
                            <Image fluid={materialProjects} />
                        </OptionsContainer>
                    </CardCol>
                </ProjectOverviewCard>

                <ProjectOverviewCard>
                    <CardCol>
                        <Image alt='Project image' fluid={imageData} />
                    </CardCol>
                    <CardCol>
                        <p>
                            Lorem ipsum, dolor sit amet consectetur adipisicing elit. Commodi repudiandae sed aperiam ut! Mollitia hic aliquid eaque, consequatur sit illo?
                            Gumbo beet greens corn soko endive gumbo gourd. Parsley shallot courgette tatsoi pea sprouts fava bean collard greens dandelion okra wakame tomato. Dandelion cucumber earthnut pea peanut soko zucchini.
                        </p>
                        <OptionsContainer>
                            <Image fluid={materialProjects} />
                        </OptionsContainer>
                    </CardCol>
                </ProjectOverviewCard>
            </ContentContainer>

            <Heading>Materialy</Heading>
        </ProjectOverviewContainer>
    )
}

export default ProjectOverview
