import React from 'react'
import styled from 'styled-components'

import Image from 'gatsby-image'
import { graphql, useStaticQuery } from 'gatsby'

export const AboutSectionContainer = styled.section`
    position: relative;
    background-color: lightgray;
    display: flex;
    height: 60rem;
`

export const Heading = styled.h2`
    position:absolute;
    font-size: 6rem;
    z-index: 99;
    left: 33rem;
    top: 8rem;
    text-transform: uppercase;
    font-weight: 900;

    span {
        color: #fff;
    }
`

export const ImageCol = styled.div`
    width: 50rem;
    overflow: hidden;

`

export const ImageContainer = styled.div`
    width: 100rem;
`

export const ContentCol = styled.div`
    width: 80%;
    padding: 4rem;

    p {
        max-width: 100rem;
        margin-top: 20rem;
    }
`

const AboutSection = () => {
    const data = useStaticQuery(graphql`
        query {
            desktop: file(relativePath: { eq: "products_overview.jpg" }) {
            childImageSharp {
                fluid {
                ...GatsbyImageSharpFluid_withWebp
                }
            }
            }
        }
    `)

    const imageData = data.desktop.childImageSharp.fluid

    return (
        <AboutSectionContainer>
            <Heading>
                <span>Ako</span> som začal
            </Heading>
            <ImageCol>
                <ImageContainer>
                    <Image alt='about image' fluid={imageData} />
                </ImageContainer>
            </ImageCol>
            <ContentCol>
                <p>
                    Turnip greens yarrow ricebean rutabaga endive cauliflower sea lettuce kohlrabi amaranth water spinach avocado daikon napa cabbage asparagus winter purslane kale. Celery potato scallion desert raisin horseradish spinach carrot soko. Lotus root water spinach fennel kombu maize bamboo shoot green bean swiss chard seakale pumpkin onion chickpea gram corn pea. Brussels sprout coriander water chestnut gourd swiss chard wakame kohlrabi beetroot carrot watercress. Corn amaranth salsify bunya nuts nori azuki bean chickweed potato bell pepper artichoke.
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Pariatur asperiores, ratione magni ab excepturi impedit aliquid deserunt voluptatem suscipit delectus natus recusandae maiores vero. Suscipit tenetur asperiores nesciunt ea aspernatur!
                </p>
            </ContentCol>
        </AboutSectionContainer>
    )
}

export default AboutSection
